import axios from 'axios';
import session from '@/store/modules/sessionModule';


const URL  =  'http://apiprofit.test:8000/'
const http = axios.create({
  baseURL: URL,
});

let isAlreadyFetchingAccessToken = false;

http.interceptors.request.use( (config) => {
 
  // Optional headers
  if (localStorage.getItem('_token')) {
    const token = localStorage.getItem('_token');
    config.headers.Authorization = `Bearer ${token}`;
  }

  return config;

}, (error) => {
  return Promise.reject(error);
});


http.interceptors.response.use( (response) => {
  return response;
}, async (error: any) => {
 
  const { config, response: { status } } = error;
  const originalRequest = config;
  let isRefreshOk: boolean = false;

  if (status === 401) {
    if (!isAlreadyFetchingAccessToken) {
      isAlreadyFetchingAccessToken = true;
      isRefreshOk = await session.getRefreshToken();
      isAlreadyFetchingAccessToken = false;
    }

    if (isRefreshOk) {
      const retryOriginalRequest = new Promise((resolve) => {
          resolve(http(originalRequest));
      });
      return retryOriginalRequest; // Return the resposne from original request with new token
    } else {
      return Promise.reject(error); // Return original response
    }
  } else { // Non 498 response
   return Promise.reject(error);
  }
});


export { http, URL};

