import {
  getModule,
  Module,
  MutationAction,
  Mutation,
  Action,
  VuexModule,
} from 'vuex-module-decorators';
import store from '@/store';
import { UserSubmit } from '../interfaces/Auth';
import { UserToken } from '../interfaces/UserToken';
import { http } from '@/utils/http';
import { User } from '../interfaces/User';
import { fetchUser, fetchMenu, fetchSubMenu } from '@/utils/utility'
import { Menu } from '../interfaces/Menu';
import { SubMenu } from '../interfaces/SubMenu';

@Module({
  namespaced: true,
  name: 'session',
  store,
  dynamic: true,
})

class sessionModule extends VuexModule {

  token: string | null = localStorage.getItem('_token');
  refresh_token: string | null = localStorage.getItem('_refresh_token');
  tokens: UserToken[] = [];
  userSesion: User[] = [];
  userMenu: Menu[] = [];
  userSubMenu: SubMenu[] = [];

  get getTokens(): UserToken[] {
    return this.tokens;
  }

  @Mutation
  setTokens(tokens: UserToken[]) {
    this.tokens = tokens;
  }

  @Mutation
  setToken(token: string) {
    this.token = token;
  }
  @Mutation
  setRefresh_token(refresh_token: string) {

    this.refresh_token = refresh_token;
  }
  @Mutation
  destroyTokens() {
    this.tokens = [];
  }
  @Mutation
  destroyToken() {
    this.token = null;
    this.refresh_token = null;
  }
  @Action
  async login(userLogin: UserSubmit) {
    await http
      .post('login', userLogin)
      .then((payload: any) => {
        if (payload) {
          userLogin.code = payload.data.code
          userLogin.message = payload.data.message
          if (userLogin.code === 200) {
            const stoken: string = payload.data.data.access_token;
            const srefresh_token: string = payload.data.data.refresh_token;
            localStorage.setItem('_token', stoken);
            localStorage.setItem('_refresh_token', srefresh_token);
            this.context.commit('setToken', stoken);
            this.context.commit('setRefresh_token', srefresh_token);
            this.context.commit('setTokens', payload.data.data);

          }
        } else {
          userLogin.code = 500;
          userLogin.message = 'Error grave con Servidor';
        }
      })
      .catch((e) => {
        userLogin.code = 500;
        userLogin.message = 'Error 500 grave con Servidor ';
      });
    return userLogin;
  }

  @Action
  async logout() {
    http.defaults.headers.common['Authorization'] = 'Bearer ' + this.token;
    await http
      .post('logout')
      .then((payload: any) => {
        localStorage.removeItem('_token');
        localStorage.removeItem('_refresh_token');
        this.context.commit('destroyToken');
        this.context.commit('destroyTokens');
        return payload;
      })
      .catch((e) => console.log('Error ${e}'));
  }

  @Action
  async getRefreshToken() {
    const stoken: string = ''
    await http
      .post('refreshtoken',
        {
          'refresh_token': this.refresh_token
        })
      .then((payload: any) => {
        if (payload) {
          if (payload.data.code === 200) {
            const stoken: string = payload.data.data.access_token;
            const srefresh_token: string = payload.data.data.refresh_token;
            localStorage.setItem('_token', stoken);
            localStorage.setItem('_refresh_token', srefresh_token);
            this.context.commit('setToken', stoken);
            this.context.commit('setRefresh_token', srefresh_token);
          }
        }
      })
      .catch((err) => {
        console.log('Error 500 grave con Servidor ');
        return false;
      });
    return true;
  }

  @MutationAction({ mutate: ['userSesion', 'userMenu', 'userSubMenu'] })
  async loadUser() {
    const user = await fetchUser();
    const userMenu = await fetchMenu();
    const userSubMenu = await fetchSubMenu(user.role_id);
    return { userSesion: user, userMenu, userSubMenu };
  }

}
export default getModule(sessionModule);
